html, body {height: 100%;
    color:#333}

#root {
    height:100%
}

#app {
    display: flex;
    align-items:stretch;
    height:100%;
}

h1 {
    padding-bottom: 16px;
    border-bottom: 1px solid #d2d2d7
}

/* Sidebar */

#sidebar {
    background-color:rgb(250,250,250);
    width:200px;
    flex:none;
    display:flex;
    flex-direction: column;
    justify-content: space-between;
}

#sidebar ul {
    padding: 0px 12px;
    list-style: none;
}

#sidebar li {
    cursor: pointer;
    line-height: 1.5em;
    margin-bottom: 6px;
}

#sidebar button.current {
    background-color: rgb(230,230,230);
}

#sidebar li {
    font-size: 15px;
    font-weight: bold;
}

#sidebar li button:not(.add_pilot_button){
    text-align: left;
    width:140px;
}

#sidebar li button:not(.action, .add_pilot_button){
    text-align: left;
    width:100%;
    font-size: 16px;
    color:rgb(51,51,51);
    border-radius: 3px;
    padding:8px 15px;
    display: block;
    transition-duration: 0.18s;
    transition-timing-function: ease-in-out;
    transition-delay: 0s;
    transition-property: background-color;
}

#sidebar li button:hover:not(.action, .add_pilot_button) {
    background-color: rgb(237,237,237);
}

/* Input file */
#sidebar label input[type="file"] {
  position: absolute;
  top: -1000px;
}

#sidebar label {
    background:#06c;
    color:white !important;
    border-radius:4px;
    font-size: 14px;
    padding:6px 12px;
    font-weight: normal;
}

#sidebar label > span {
    display: inline-block;
    width: 116px
}

#content {
    flex:auto;
    padding:5px 20px;
}

button {
    background:none;
    border:0;
    cursor: pointer;
}

.add_pilot_button {
    display:inline;
    margin-left: 20px;
    padding:2px 8px;
    border-radius: 10px;
    text-align: center;
    font-weight: bold;
    background-color:#06C;
    color:white;
}

/* Toolbar */

.toolbar {
    display:flex;
    justify-content: flex-end;
}

button.action {
    background:#06c;
    color:white !important;
    border-radius:4px;
    font-size: 14px;
    padding:6px 12px;
}

button.action:hover {
    background:#0f6fcf;
}

button.action:active {
    background:#0060c0;
}

button.disabled {
    background: #CFD1D2;
    cursor: auto;
}

button.disabled:hover {
    background: #CFD1D2;
}

button.destructive_action {
    background:#c00;
    color:white !important;
    border-radius:4px;
    font-size: 14px;
    padding:6px 12px;
    float:right;
}

button.destructive_action:hover {
    background: #CC3939;
}


/* Formulaires */

select {
    font-size:16px;
    line-height:24px;
    padding:5px 10px;
    margin:0;
    width:200px;
}

input, textarea {
    border-radius:4px;
    border:1px solid #d2d2d6;
    font-size:16px;
    line-height:24px;
    padding:5px 10px;
    margin:0;
    width:calc(100% - 40px);
}

textarea {
    height:200px;
}

.form-field-input label {
    font-size:16px;
    font-weight:bold;
    margin-bottom:20px;
    margin-right:8px
}

.form-field-input .label-row {
    margin-top:20px;
    margin-bottom:.625em
}

.form-field-input .icon-questionmark {
    width:1rem;
    height:1rem;
    background-repeat:no-repeat;
    background-image:url("data:image/svg+xml;charset=utf-8,%3Csvg height='16' width='15' xmlns='http://www.w3.org/2000/svg'%3E%3Cg fill='none' fill-rule='evenodd'%3E%3Cpath d='M-750-18H317v52H-750z'/%3E%3Ctext fill='%23111' font-family='SFProDisplay-Semibold, SF Pro Display' font-size='21' font-weight='500' letter-spacing='.23' transform='translate(-750 -5)'%3E%3Ctspan x='0' y='20'%3E%3C/tspan%3E%3C/text%3E%3Cg fill-rule='nonzero'%3E%3Cpath d='M7.5 15.5c4.125 0 7.5-3.375 7.5-7.5S11.625.5 7.5.5 0 3.875 0 8s3.375 7.5 7.5 7.5z' fill='%23f2f2f2'/%3E%3Cpath d='M6.365 10.739c0-.493.406-.83.968-.83.581 0 .969.337.969.83 0 .483-.388.83-.969.83-.562 0-.968-.347-.968-.83zm.276-1.522v-.415c-.058-.793.396-1.296.969-1.799.547-.27.894-.512.83-.969.064-.674-.457-.944-.969-.968-.762.024-1.139.526-1.106 1.107H4.98c0-1.634 1.294-2.214 2.49-2.214 1.342 0 2.491.657 2.491 1.937 0 .962-.58 1.397-1.107 1.799-.67.449-.9.613-.968 1.245v.277z' fill='%23888'/%3E%3C/g%3E%3C/g%3E%3C/svg%3E")
