/* Tooltips */

.tooltip-wrapper {
    position:relative;
    display: inline-block;
}

.tooltip {
    font-size: 13px;
    background-color: #f5f5f7;
    padding:10px;
    border-radius: 4px;
    z-index:2;
    position:absolute;
    left:-10px;
    bottom:30px;
    display:none;
    width:200px;
}

.tooltip-wrapper .tooltip:after {
    position: absolute;
    bottom: -10px;
    left: 10px;
  border-top: 10px solid #f5f5f7;
  border-right: 10px solid transparent;
  border-left: 10px solid transparent;
  content: " ";
}

.shown {
    display:block;
}

/* Tabs */

.tabButton {
    list-style-type: none;
    margin:20px auto 40px;
    padding:9px 2px;
    border-bottom: 1px solid #eee;
    
    display: flex;
    flex-flow: row wrap;
    justify-content: center
}

.tablink {
    padding:6px 10px;
    font-size: 14px;
    cursor:pointer
}

.tablink:hover {
    border-radius:4px;
    background-color:#eee;
}

.tablink > * {
    padding:6px 10px;
}

.current {
    color:#06c
}

/* Tables */


table {
    border-collapse: collapse;
    text-align: left;
    width:90%;
    margin:auto
}

table th {
    font-size:17px;
    padding: 2em .5em .5em 1em;
}

table thead tr th {
  font-weight:600;
  color: #333;
  padding: .5em 1em .5em 1em;
  border-bottom: 1px solid #333;
}

table tr td {
    border-bottom: .5px solid #e6e6e6;
    color: #666;
    padding: .25em 1em .25em 1em;
    vertical-align:center;
    height:20px;
}

.selected {
    background-color: #06c;
}

.selected td {
    color:white;
}

/* Master > Detail */

.master tbody {
    display: block;
    width: 100%;
    height:300px;
    overflow:scroll;
}

.master thead, tbody tr {
    display: table;
    width: 100%;
    table-layout: fixed;/* even columns width , fix width of table too*/
}

.two-columns {
    display: flex;
    justify-content:space-between;
}

.two-columns > div {
    flex-grow: 1
}